import React from 'react'
import {
   Card,
   CardGrid,
   Group,
   NavIdProps,
   Panel,
   PanelHeader,
   PanelHeaderBack,
   PanelHeaderSubmit,
   Separator,
   SimpleCell,
   Switch,
} from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { setAnimations, setAutoLockScreen, setComplexitySelection, setSounds, setVibration } from 'store/slices/state'

export const PanelSettings: React.FC<NavIdProps> = ({ nav }) => {
   const router = useRouter()
   const dispatch = useAppDispatch()
   const { sounds, vibration, autoLockScreen, animations, complexitySelection } = useAppSelector(
      (state) => state.settings
   )

   const onClickBack = () => {
      router.popPage()
   }
   const onClickSubmit = () => {
      router.popPageTo(-2)
   }

   const onChangeSounds = (e: React.ChangeEvent<HTMLInputElement>) => {
      dispatch(setSounds(e.target.checked))
   }
   const onChangeVibration = (e: React.ChangeEvent<HTMLInputElement>) => {
      dispatch(setVibration(e.target.checked))
   }
   const onChangeAutoLockScreen = (e: React.ChangeEvent<HTMLInputElement>) => {
      dispatch(setAutoLockScreen(e.target.checked))
   }
   const onChangeAnimations = (e: React.ChangeEvent<HTMLInputElement>) => {
      dispatch(setAnimations(e.target.checked))
   }
   const onChangeComplexitySelection = (e: React.ChangeEvent<HTMLInputElement>) => {
      dispatch(setComplexitySelection(e.target.checked))
   }

   return (
      <Panel nav={nav}>
         <PanelHeader
            separator={false}
            before={<PanelHeaderBack onClick={onClickBack} />}
            after={<PanelHeaderSubmit onClick={onClickSubmit} />}
         >
            Настройки
         </PanelHeader>
         <PanelHeaderSeparator />
         <Group>
            <Group mode='plain' separator='hide'>
               <CardGrid size='l'>
                  <Card mode='tint'>
                     <SimpleCell disabled after={<Switch checked={sounds} onChange={onChangeSounds} />}>
                        Звуки
                     </SimpleCell>
                     <Separator wide />
                     <SimpleCell disabled after={<Switch checked={vibration} onChange={onChangeVibration} />}>
                        Вибрация
                     </SimpleCell>
                     <Separator wide />
                     <SimpleCell disabled after={<Switch checked={autoLockScreen} onChange={onChangeAutoLockScreen} />}>
                        Блокировка экрана
                     </SimpleCell>
                  </Card>
               </CardGrid>

               <CardGrid size='l'>
                  <Card mode='tint'>
                     <SimpleCell disabled after={<Switch checked={animations} onChange={onChangeAnimations} />}>
                        Анимации
                     </SimpleCell>
                     <Separator wide />
                     <SimpleCell
                        disabled
                        after={<Switch checked={complexitySelection} onChange={onChangeComplexitySelection} />}
                     >
                        Выбор сложности
                     </SimpleCell>
                  </Card>
               </CardGrid>
            </Group>
         </Group>
      </Panel>
   )
}
