import React from 'react'
import { ActionSheet, ActionSheetItem, NavIdProps } from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { Icon28Favorite, Icon28LockOpenOutline, Icon28LockOutline } from '@vkontakte/icons'
import { useAnchorElement } from '../AnchorElementProvider'

export const ActionSheetComplexitySelection: React.FC<NavIdProps> = () => {
   const router = useRouter()
   const anchorElement = useAnchorElement()

   const onClose = () => {
      router.popPageIfPopup()
   }

   return (
      <ActionSheet
         toggleRef={anchorElement}
         onClose={onClose}
         iosCloseItem={
            <ActionSheetItem mode='cancel' autoclose>
               Отмена
            </ActionSheetItem>
         }
      >
         <ActionSheetItem before={<Icon28Favorite fill='var(--vkui--color_accent_green)' />} autoclose>
            Лёгкий
         </ActionSheetItem>
         <ActionSheetItem before={<Icon28Favorite fill='var(--vkui--color_accent_orange)' />} autoclose>
            Средний
         </ActionSheetItem>
         <ActionSheetItem before={<Icon28Favorite fill='var(--vkui--color_accent_red)' />} autoclose>
            Сложный
         </ActionSheetItem>
         <ActionSheetItem
            // before={<Icon28LockOpenOutline />}
            // before={<Icon28LockOutline />}
            before={<Icon28Favorite fill='var(--vkui--color_accent_violet)' />}
            autoclose
            // disabled
            // subtitle='Откроется после достижения 4 ранга'
         >
            Экспертный
         </ActionSheetItem>
      </ActionSheet>
   )
}
