import React from 'react'
import { IBottomCell } from 'store/slices/state'
import { CellGroup } from '../CellGroup/CellGroup'
import { SquareCell } from '../SquareCell/SquareCell'
import { CellLetter, CellLetterProps } from '../CellLetter/CellLetter'
import { Letter, LetterProps } from '../Letter/Letter'
import { BaseCellsRowProps } from './GameCells'

interface CellsRowBottomProps extends BaseCellsRowProps<IBottomCell> {
   multiline?: boolean
}
const transformCells = (cells: IBottomCell[], mult?: boolean) => {
   const result: IBottomCell[][] = []

   if (mult) {
      const mid = Math.floor(cells.length / 2)

      const top = cells.slice(0, mid)
      const bottom = cells.slice(mid)

      // console.log(top, bottom)

      result.push(cells, cells)
      // result.push(top, bottom)
   } else {
      result.push(cells)
   }
   return result
}

export const CellsRowBottom: React.FC<CellsRowBottomProps> = ({
   cells: cellsProp,
   cellsRef,
   onClickLetter: _onClickLetter,
   onTransitionLetterRun,
   onTransitionLetterEnd,
   animatedLettersData,
   multiline = true,
}) => {
   const cellsRows = transformCells(cellsProp, multiline)

   const renderCell = (cell: IBottomCell) => {
      const key = cell.id
      const isAnimate = cell.currentLetter !== null && !!animatedLettersData[cell.currentLetter.id]
      const hasLetter = !!cell.currentLetter

      const onClickLetter = cell.currentLetter && !isAnimate ? _onClickLetter : undefined

      const cellProps: CellLetterProps = {
         mode: undefined,
      }

      const letterProps: LetterProps = {
         ...cell.letter,
         hasBackground: true,
         mode: 'primary',
         onClickLetter,
         onTransitionLetterRun,
         onTransitionLetterEnd,
      }

      if (isAnimate) {
         const animatedLetter = animatedLettersData[cell.currentLetter!.id]

         const fromCell = cellsRef[animatedLetter.fromCellId]
         const toCell = cellsRef[animatedLetter.toCellId]

         if (fromCell && toCell) {
            const fromRect = fromCell.getBoundingClientRect()
            const toRect = toCell.getBoundingClientRect()

            const x = fromRect.x - toRect.x
            const y = fromRect.y - toRect.y

            letterProps.style = {
               transform: `translate(${x}px, ${y}px)`,
            }
         }
      }

      return (
         <SquareCell
            getRef={(el) => {
               cellsRef[cell.id] = el
            }}
            // gap
            key={key}
         >
            <CellLetter {...cellProps}>{hasLetter && <Letter {...letterProps} />}</CellLetter>
         </SquareCell>
      )
   }

   return (
      <CellGroup mode='vertical' stretched gap='l' verticalPadding='l' className='CellsRow--bottom'>
         {cellsRows.map((row, idx) => (
            <CellGroup
               stretched
               gap='m'
               // verticalPadding='s'
               key={idx}
            >
               {row.map(renderCell)}
            </CellGroup>
         ))}
      </CellGroup>
   )
   if (multiline) {
   }

   // return (
   //    <CellsRow stretched className='CellsRow--bottom'>
   //       {cells.map(renderCell)}
   //    </CellsRow>
   // )
}
