import React from 'react'
import {
   AppearanceProvider,
   Button,
   ButtonGroup,
   classNames,
   FixedLayout,
   Group,
   Headline,
   IconButton,
   NavIdProps,
   Panel,
   PanelHeader,
   PanelHeaderBack,
   PromoBanner,
} from '@vkontakte/vkui'
import { Icon28Backspace } from '@vkontakte/icons'
import { useRouter } from '@happysanta/router'
import { PAGE_OPTIONS } from 'router/pages'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { getPromoBannerData, moveAllLettersDown, startGame } from 'store/slices/state'
import { RiddleDescription } from 'components/ui/RiddleDescription/RiddleDescription'
import { GameHeader } from 'components/ui/GameHeader/GameHeader'

import bridge from '@vkontakte/vk-bridge'

import { GameCells } from 'components/ui/Cells/GameCells'

import { Icon28BackspaceOutline } from '@vkontakte/icons'
import { Icon28CoinsOutline } from '@vkontakte/icons'

import { Icon28Crown, Icon28CrownOutline } from '@vkontakte/icons'

import { Icon28HelpOutline, Icon24Help } from '@vkontakte/icons'
import { numberFormatter } from 'helpers/format'
import { GameActions } from 'components/ui/GameActions/GameActions'
import { useSetAnchorElement } from '../popout/AnchorElementProvider'
import { defaultPromoBannerData } from 'helpers/vkui'
import { useAdaptivityIsDesktop } from '@vkontakte/vkui/dist/hooks/useAdaptivity'

export const PanelGame: React.FC<NavIdProps> = ({ nav }) => {
   const dispatch = useAppDispatch()
   const router = useRouter()
   const setAnchorElement = useSetAnchorElement()

   const isDesktop = useAdaptivityIsDesktop()

   const bannerData = useAppSelector((state) => state.promoBannerData)

   const level = useAppSelector((state) => state.game.level)
   const cells = useAppSelector((state) => state.game.cells.top)

   React.useEffect(() => {
      dispatch(getPromoBannerData())
      //@ts-ignore
      window.bridge = bridge

      // console.log(localStorage.getItem('key'), new Date().toJSON())

      // localStorage.setItem('key', new Date().toJSON())
   }, [])

   React.useEffect(() => {
      dispatch(startGame())
   }, [dispatch])

   const onClickBack = () => {
      router.popPage()
   }
   const onClickOptions = () => {
      router.pushPage(PAGE_OPTIONS)
   }

   return (
      <Panel nav={nav} className={classNames('Panel--no-tabbar')}>
         <PanelHeader
            before={<PanelHeaderBack onClick={onClickBack} />}
            after={
               <Button
                  onClick={(e) => {
                     setAnchorElement(e.currentTarget)
                     router.pushPopup('crown_balance')
                  }}
                  size='l'
                  mode='tertiary'
                  before={<Icon28Crown width={28} height={28} />}
               >
                  {numberFormatter.format(1014123124)}
               </Button>
            }
            separator={isDesktop}
         >
            Уровень {level}
         </PanelHeader>
         <Group className='GameGroup'>
            <RiddleDescription />
            <GameCells />
            <GameActions />
         </Group>
         {/* <div style={{ position: 'absolute', top: 0, bottom: 0, left: 0, right: 0 }}>
            <CellsWrapper>
               <CellsRow>
                  {cells.map((cell) => (
                 
                  ))}
               </CellsRow>
            </CellsWrapper>
         </div> */}
         <div style={{ visibility: 'hidden' }}>
            <PromoBanner bannerData={defaultPromoBannerData} onClose={() => {}} />
         </div>
         {!bannerData && (
            <FixedLayout vertical='bottom' style={{ paddingBottom: 'var(--safe-area-inset-bottom)' }}>
               <PromoBanner bannerData={defaultPromoBannerData} onClose={() => {}} />
            </FixedLayout>
         )}
         {!!bannerData && (
            <FixedLayout vertical='bottom' style={{ paddingBottom: 'var(--safe-area-inset-bottom)' }}>
               <PromoBanner bannerData={bannerData} onClose={() => {}} />
            </FixedLayout>
         )}
      </Panel>
   )
}
