import React from 'react'
import { classNames } from '@vkontakte/vkui'

import './CellLetter.css'

export interface CellLetterProps {
   mode?: 'outline' | 'shadow'
   hasBackground?: boolean
   style?: React.HTMLAttributes<HTMLDivElement>['style']
}

export const CellLetter: React.FC<CellLetterProps> = React.memo(({ mode, style, hasBackground, children }) => {
   return (
      <div
         className={classNames(
            'CellLetter',
            hasBackground && 'CellLetter--has-background',
            !!mode && `CellLetter--md-${mode}`
         )}
         style={style}
      >
         {children}
      </div>
   )
})
