import React from 'react'
import { Icon28Notification, Icon28Notifications } from '@vkontakte/icons'
import {
   Div,
   Group,
   Header,
   NavIdProps,
   Panel,
   PanelHeader,
   Progress,
   SimpleCell,
   Switch,
   Title,
} from '@vkontakte/vkui'

// const dateTimeFormat = new Intl.DateTimeFormat(undefined, {
//    day: 'numeric',
//    hour: 'numeric',
//    minute: 'numeric',
//    second: 'numeric',
//    formatMatcher: 'best fit',
// })

export const PanelStatistics: React.FC<NavIdProps> = ({ nav }) => {
   return (
      <Panel nav={nav}>
         <PanelHeader separator={false}></PanelHeader>
         <Group>
            <Div>
               <div style={{ display: 'flex', justifyContent: 'center', marginBottom: 8 }}>
                  <Title level='2' weight='2'>
                     НОВИЧОК
                  </Title>
               </div>
               <Progress value={40} />
               <div style={{ display: 'flex', justifyContent: 'center', marginTop: 4 }}>
                  <Title level='2' weight='3'>
                     1 200 / 100 000
                  </Title>
               </div>
            </Div>
            <SimpleCell
               indicator={new Date().toLocaleDateString(undefined, {
                  year: 'numeric',
                  month: 'long',
                  day: 'numeric',
               })}
            >
               В игре с:
            </SimpleCell>

            {/* <SimpleCell indicator={'02:15:30'}>Время в игре:</SimpleCell> */}

            <SimpleCell indicator={20} expandable>
               Cлова
            </SimpleCell>
            {/* 
            <SimpleCell indicator={2}>Всего игр:</SimpleCell> */}

            {/* <Group separator='hide' mode='plain' header={<Header>Настройки</Header>}></Group>
            <Group mode='plain'>
               <SimpleCell disabled before={<Icon28Notification />} after={<Switch />}>
                  Оповещения
               </SimpleCell>
               
            </Group> */}
         </Group>
      </Panel>
   )
}
