import { useRouter } from '@happysanta/router'
import { Icon16Crown } from '@vkontakte/icons'
import { ActionSheet, ActionSheetItem, NavIdProps } from '@vkontakte/vkui'
import { useAppDispatch } from 'hooks/redux'
import React from 'react'
import { ActionSheetCloseItem } from '../ActionSheetCloseItem'
import { useAnchorElement } from '../AnchorElementProvider'

export const ActionSheetGameHelp: React.FC<NavIdProps> = () => {
   const router = useRouter()
   const dispatch = useAppDispatch()
   const anchorElement = useAnchorElement()

   const onClose = () => {
      router.popPageIfPopup()
   }

   const meta = (
      <div
         style={{
            display: 'flex',
            alignItems: 'center',
            color: 'var(--vkui--color_accent_blue)',
            borderRadius: 25,
            backgroundColor: 'var(--vkui--color_background_modal)',
            padding: '2px 8px',
         }}
      >
         <div>{Math.floor(Math.random() * 500)}</div>
         <Icon16Crown style={{ marginLeft: 4 }} width={16} height={16} />
      </div>
   )

   const meta2 = (
      <div
         style={{
            display: 'flex',
            alignItems: 'center',
            color: 'var(--vkui--color_accent_blue)',
            borderRadius: 25,
            backgroundColor: 'var(--vkui--color_background_modal)',
            padding: '2px 8px',
         }}
      >
         {/* <Icon16Crown width={16} height={16} /> */}
         <div
         //  style={{ marginLeft: 4 }}
         >
            {Math.floor(Math.random() * 100)}
         </div>
      </div>
   )

   return (
      <ActionSheet
         //  header='Подсказки'
         //  text='text'
         toggleRef={anchorElement}
         iosCloseItem={<ActionSheetCloseItem />}
         onClose={onClose}
      >
         <ActionSheetItem meta={meta2}>Показать слово</ActionSheetItem>
         <ActionSheetItem meta={meta}>Убрать лишние буквы</ActionSheetItem>
         <ActionSheetItem meta={meta}>Показать букву</ActionSheetItem>
      </ActionSheet>
   )
}
