import { classNames } from '@vkontakte/vkui'
import React from 'react'

import './CellsWrapper.css'

interface CellsWrapperProps {
   verticalPadding?: 's' | 'm' | 'l'
   stretched?: boolean
}

export const CellsWrapper: React.FC<CellsWrapperProps> = ({ children, stretched, verticalPadding }) => {
   return (
      <div
         className={classNames(
            'CellsWrapper',
            stretched && 'CellsWrapper--stretched',
            verticalPadding !== undefined && `CellsWrapper--v-padding-${verticalPadding}`
         )}
      >
         <div className='CellsWrapper__in'>{children}</div>
      </div>
   )
}
