import React from 'react'
import { classNames, useAdaptivity } from '@vkontakte/vkui'
import { ILetter } from 'store/slices/state'

import './Letter.css'
import { LetterHandlers } from '../Cells/GameCells'

export interface LetterProps extends ILetter, LetterHandlers {
   /**
    * @param primary - для буквы с фоном
    * @param secondary - для подсказки
    * @default 'primary'
    */
   mode?: 'primary' | 'secondary'

   /**
    * @default true
    */
   hasBackground?: boolean
   style?: React.HTMLAttributes<HTMLDivElement>['style']
   className?: React.HTMLAttributes<HTMLDivElement>['className']
}
export const Letter: React.FC<LetterProps> = ({
   hasBackground = true,
   mode = 'primary',
   value,
   color,
   id,
   style,
   className,
   onTransitionLetterRun,
   onTransitionLetterEnd,
   onClickLetter,
}) => {
   const [_style, _setStyle] = React.useState(style)

   const canvasRef = React.useRef<HTMLCanvasElement>(null)
   const letterRef = React.useRef<HTMLDivElement>(null)

   const { hasMouse } = useAdaptivity()

   const renderLetter = React.useCallback(() => {
      if (!(canvasRef.current instanceof HTMLCanvasElement)) return

      const ctx = canvasRef.current.getContext('2d')

      if (!ctx) return

      const clientWidth = canvasRef.current.clientWidth
      const clientHeight = canvasRef.current.clientHeight

      const canvasWidth = clientWidth * devicePixelRatio
      const canvasHeight = clientHeight * devicePixelRatio

      canvasRef.current.width = canvasWidth
      canvasRef.current.height = canvasHeight

      const fontSize = canvasRef.current.height * 0.7

      ctx.font = `700 ${fontSize}px/1 -apple-system,system-ui,Helvetica Neue,Roboto,sans-serif`

      ctx.textAlign = 'center'
      // ctx.textBaseline = 'middle'
      ctx.textBaseline = 'alphabetic'

      //#818C99 - secondary
      //#99A2AD - tertiary

      ctx.fillStyle = mode === 'primary' ? '#fff' : '#818C99'
      if (value) {
         ctx.fillText(
            value,
            canvasRef.current.width / 2,
            // canvasRef.current.height / 2
            canvasRef.current.height - fontSize / 3
         )
      } else {
         ctx.clearRect(0, 0, canvasWidth, canvasHeight)
      }
   }, [mode, value])

   React.useEffect(() => {
      renderLetter()
   }, [renderLetter])

   React.useLayoutEffect(() => {
      if (!(letterRef.current instanceof HTMLElement)) return

      letterRef.current.addEventListener('transitionrun', _onTransitionRun, { passive: true })

      if (style?.transform) {
         _setStyle({})
      }

      const letter = letterRef.current
      return () => {
         letter.removeEventListener('transitionrun', _onTransitionRun)
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
   }, [])

   const _onClick = (e: React.MouseEvent) => {
      onClickLetter && onClickLetter(id)
   }

   const _onTransitionRun = (e: TransitionEvent) => {
      onTransitionLetterRun && onTransitionLetterRun(id)
   }
   const _onTransitionEnd = (e: React.TransitionEvent) => {
      onTransitionLetterEnd && onTransitionLetterEnd(id)
   }

   return (
      <div
         className={classNames(
            'Letter',
            className,
            !!onClickLetter && hasMouse && 'Letter--has-hover',
            hasBackground && `Letter--clr-${color}`
         )}
         style={_style}
         ref={letterRef}
         onClick={_onClick}
         onTransitionEnd={_onTransitionEnd}
      >
         <canvas ref={canvasRef} />
      </div>
   )
}
