interface CellsRowTopProps
   extends BaseCellsRowProps<ITopCell>,
      Pick<ShakingCellGroupProps, 'onShakeRun' | 'onShakeEnd'> {}

export const CellsRowTop: React.FC<CellsRowTopProps> = React.memo(
   ({
      cells,
      cellsRef,
      onClickLetter: _onClickLetter,
      onTransitionLetterRun,
      onTransitionLetterEnd,
      animatedLettersData,

      onShakeRun,
      onShakeEnd,
   }) => {
      // React.useEffect(() => {
      // const solution = checkSolution(cells)
      // if (solution === SolutionEnum.SUCCES) {
      // } else if (solution === SolutionEnum.FAIL) {
      //    window.dispatchEvent(new CustomEvent(RUN_SHAKING_EVENT))
      //    // dispatch(moveAllLettersDown())
      // }
      // }, [])

      const renderCell = (cell: ITopCell) => {
         const key = cell.id

         const isCurrentLetter = cell.currentLetter !== null
         const isAnimate = cell.currentLetter !== null && !!animatedLettersData[cell.currentLetter.id]

         const onClickLetter = isCurrentLetter && !isAnimate ? _onClickLetter : undefined

         const hasLetter = isCurrentLetter || !!cell.letterIsShown

         const letter = (isCurrentLetter ? cell.currentLetter : cell.letter) as ILetter

         const cellProps: CellLetterProps = {
            mode: 'outline',
            hasBackground: true,
         }

         const letterProps: LetterProps = {
            ...letter,
            mode: !isCurrentLetter && !!cell.letterIsShown ? 'secondary' : 'primary',
            hasBackground: isCurrentLetter || isAnimate,

            onTransitionLetterRun,
            onTransitionLetterEnd,
            onClickLetter,
         }

         if (isAnimate) {
            const animatedLetter = animatedLettersData[cell.currentLetter!.id]

            const fromCell = cellsRef[animatedLetter.fromCellId]
            const toCell = cellsRef[animatedLetter.toCellId]

            if (fromCell && toCell) {
               const fromRect = fromCell.getBoundingClientRect()
               const toRect = toCell.getBoundingClientRect()

               const x = fromRect.x - toRect.x
               const y = fromRect.y - toRect.y

               letterProps.style = {
                  transform: `translate(${x}px, ${y}px)`,
               }
            }
         }

         return (
            <SquareCell
               getRef={(el) => (cellsRef[cell.id] = el)}
               key={key}
               // gap
            >
               <CellLetter {...cellProps}>
                  {cell.letterIsShown && (
                     <Letter
                        id={cell.letter.id}
                        color={cell.letter.color}
                        value={cell.letter.value}
                        hasBackground={false}
                        mode='secondary'
                     />
                  )}
                  {hasLetter && <Letter {...letterProps} />}
               </CellLetter>
            </SquareCell>
         )
      }
      return (
         <ShakingCellGroup
            gap='m'
            verticalPadding='l'
            className='CellsRow--top'
            onShakeRun={onShakeRun}
            onShakeEnd={onShakeEnd}
         >
            {cells.map(renderCell)}
         </ShakingCellGroup>
      )
   }
)
