import React from 'react'
import { classNames } from '@vkontakte/vkui'

import './CellGroup.css'

export interface CellGroupProps extends React.AllHTMLAttributes<HTMLElement> {
   stretched?: boolean
   gap?: 's' | 'm' | 'l'
   verticalPadding?: 's' | 'm' | 'l'
   /**
    * @default `horizontal`
    */
   mode?: 'vertical' | 'horizontal'
}

export const CellGroup: React.FC<CellGroupProps> = ({
   children,
   className,
   stretched,
   verticalPadding,
   mode = 'horizontal',
   gap,
   ...restProps
}) => {
   return (
      <div
         {...restProps}
         className={classNames(
            'CellGroup',
            `CellGroup--md-${mode}`,
            verticalPadding !== undefined && `CellGroup--v-padding-${verticalPadding}`,
            gap !== undefined && `CellGroup--gap-${gap}`,
            stretched && 'CellGroup--stretched',
            className
         )}
      >
         {children}
         {/* <div className='CellGroup__in'></div> */}
      </div>
   )
}
