import React from 'react'
import { ActionSheetItem, ActionSheetItemProps } from '@vkontakte/vkui'

interface ActionSheetCloseItemProps extends ActionSheetItemProps {}

export const ActionSheetCloseItem: React.FC<ActionSheetCloseItemProps> = ({
   mode,
   children = 'Отмена',
   ...restProps
}) => {
   return (
      <ActionSheetItem mode='cancel' autoclose {...restProps}>
         {children}
      </ActionSheetItem>
   )
}
