import React from 'react'
import { classNames, IconButton } from '@vkontakte/vkui'

import './GameActions.css'
import { Icon24Help, Icon28Backspace } from '@vkontakte/icons'
import { useAppDispatch } from 'hooks/redux'
import { useRouter } from '@happysanta/router'
import { moveAllLettersDown } from 'store/slices/state'

export const GameActions: React.FC = () => {
   const dispatch = useAppDispatch()
   const router = useRouter()

   const onClickHelp = () => {
      router.pushPopup('help')
   }

   const onClickMoveAllDown = () => {
      dispatch(moveAllLettersDown())
   }
   return (
      <div className={classNames('GameActions')}>
         <div className='GameActions__in'>
            <IconButton onClick={onClickHelp} className='GameAction GameAction--left'>
               <Icon24Help width={28} height={28} />
            </IconButton>
            <IconButton onClick={onClickMoveAllDown} className='GameAction GameAction--right'>
               <Icon28Backspace width={28} height={28} />
            </IconButton>
         </div>
      </div>
   )
}
