import React from 'react'
import { classNames } from '@vkontakte/vkui'
import { CellGroup, CellGroupProps } from './CellGroup'

import './ShakingCellGroup.css'

export interface ShakingCellGroupProps extends CellGroupProps {
   onShakeRun?: () => void
   onShakeEnd?: () => void
}

export const RUN_SHAKING_EVENT = 'runshakingcells'

export const ShakingCellGroup: React.FC<ShakingCellGroupProps> = ({
   children,
   className,

   onShakeRun,
   onShakeEnd,
   onAnimationEnd,
   ...restProps
}) => {
   const [shaking, setShaking] = React.useState(false)

   React.useEffect(() => {
      window.addEventListener(RUN_SHAKING_EVENT, _onShakeRun)

      return () => {
         window.removeEventListener(RUN_SHAKING_EVENT, _onShakeRun)
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
   }, [])

   const _onShakeRun = () => {
      setShaking(true)
      onShakeRun && onShakeRun()
   }

   const _onAnimationEnd = (e: React.AnimationEvent<HTMLElement>) => {
      onAnimationEnd && onAnimationEnd(e)
      if (e.animationName === 'horizontal-shaking') {
         setShaking(false)
         onShakeEnd && onShakeEnd()
      }
   }

   return (
      <CellGroup
         {...restProps}
         className={classNames(className, shaking && 'ShakingCellGroup--shaking')}
         onAnimationEnd={_onAnimationEnd}
      >
         {children}
      </CellGroup>
   )
}
