import React from 'react'

type AnchorElement = HTMLElement | null
type SetAnchorElement = (el: HTMLElement) => void

const AnchorElementContext = React.createContext<AnchorElement | undefined>(undefined)
const SetAnchorElementContext = React.createContext<SetAnchorElement | undefined>(undefined)

export const AnchorElementProvider: React.FC = ({ children }) => {
   const [anchorElement, _setAnchorElement] = React.useState<HTMLElement | null>(null)

   const setAnchorElement = (el: HTMLElement) => {
      _setAnchorElement(el)
   }
   return (
      <SetAnchorElementContext.Provider value={setAnchorElement}>
         <AnchorElementContext.Provider value={anchorElement}>{children}</AnchorElementContext.Provider>
      </SetAnchorElementContext.Provider>
   )
}

export const useAnchorElement = () => {
   const context = React.useContext(AnchorElementContext)
   if (context === undefined) throw new Error('Using useAnchorElement without context')
   return context
}
export const useSetAnchorElement = () => {
   const context = React.useContext(SetAnchorElementContext)
   if (context === undefined) throw new Error('Using useAnchorElement without context')
   return context
}
