import React from 'react'
import { HasRef } from '@vkontakte/vkui/dist/types'

import './SquareCell.css'
import { classNames } from '@vkontakte/vkui'

interface SquareCellProps extends HasRef<HTMLDivElement> {
   // gap?: boolean
   limitSize?: boolean
}

export const SquareCell: React.FC<SquareCellProps> = React.memo(
   ({
      getRef,
      //  gap,
      limitSize,
      children,
   }) => {
      return (
         <div
            ref={getRef}
            className={classNames(
               'SquareCell',
               //  gap && 'SquareCell--gap',
               limitSize && 'SquareCell--limit-size'
            )}
         >
            <div className='SquareCell__in'>{children}</div>
         </div>
      )
   }
)
