import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit'
import { populateGame } from 'helpers/riddle'
import { riddles } from 'riddles/riddles'

import bridge, { ErrorData } from '@vkontakte/vk-bridge'
import { RootState } from 'store'
import { PromoBannerProps } from '@vkontakte/vkui'
import { PromoBannerData } from 'types/vk'
import { defaultPromoBannerData } from 'helpers/vkui'

interface Profile {
   // hints: number
   coins: number
}

export interface Riddle {
   id: string
   complexity: number
   description: string
   word: string
}

export interface ILetter {
   id: string
   value: string
   color: string
}

export interface IGame {
   level: number
   attemptNumber: number
   riddle: Riddle

   solutionCompleted: boolean
   gameComplexity: number

   gameStartedAt: number
   gameEndsAt: number | null

   numberOfLetterInserts: number
   letters: ILetter[]
   cells: GameCells
}

export interface ICell {
   id: string
   letter: ILetter
   currentLetter: ILetter | null
}

export interface ITopCell extends ICell {
   letterIsShown?: boolean
}

export interface IBottomCell extends ICell {}

export interface GameCells {
   top: ITopCell[]
   bottom: IBottomCell[]
}

interface ISettings {
   sounds: boolean
   vibration: boolean
   autoLockScreen: boolean
   complexitySelection: boolean
   animations: boolean
}

interface IPlayer {
   createdAt: number
   countAuth: number
   crown: number
}

interface IAnimatedLetter {
   toCellId: string
   fromCellId: string
}

export type AnimatedLettersData = { [letterId: string]: IAnimatedLetter }

interface State {
   player: IPlayer
   settings: ISettings
   game: IGame

   animatedLettersData: AnimatedLettersData

   promoBannerData: PromoBannerData | null
   // promoBannerFetched: null | number
   // vkErrorData: ErrorData
}

// const rankItems = [{from: 0, to:9999, rank: '',rankTitle: 'Новичок',}]

const initialState: State = {
   player: {
      crown: 1000,
      createdAt: Date.now(),
      countAuth: 1,
   },
   settings: {
      sounds: false,
      vibration: false,
      autoLockScreen: true,
      animations: true,
      complexitySelection: false,
   },
   game: {
      level: 0,
      attemptNumber: 1,
      gameComplexity: 1,
      gameStartedAt: Date.now(),
      gameEndsAt: null,
      solutionCompleted: false,
      numberOfLetterInserts: 0,
      letters: [],
      cells: { bottom: [], top: [] },
      riddle: { ...riddles[0], complexity: 1 },
   },
   animatedLettersData: {},
   promoBannerData: null,
   // vkErrorData: {
   //    error_data: { error_code: 0, error_reason: '' },
   //    error_type: 'client_error',
   // },
}

export const getPromoBannerData = createAsyncThunk(
   'getPromoBannerData',
   async (_, thunkAPI) => {
      try {
         const supports = bridge.supports<any>('VKWebAppGetAds')

         if (supports) {
            const response = (await bridge.send<any>('VKWebAppGetAds')) as PromoBannerData
            return response
         } else {
            const response = (await bridge.send<any>('VKWebAppGetAds')) as PromoBannerData
            console.log('!supports VKWebAppGetAds', response)
         }
      } catch (e) {
         return thunkAPI.rejectWithValue(e)
      }
   }
   // {
   //    condition: (_, thunkAPI) => {
   //       const last = (thunkAPI.getState() as RootState).promoBannerFetched
   //       if (!last) return true
   //       if (Date.now() - last > 50000) return true
   //       return false
   //    },
   // }
)

export const startGame = createAsyncThunk('startGame', async (_, thunkAPI) => {
   try {
      const riddle = { ...riddles[1] }
      const game = populateGame(riddle, 1)

      // bridge.send('VKWebAppStorageSet',{key: '',value: ''}).then((data) => {})

      return game
   } catch (e) {
      return thunkAPI.rejectWithValue(e)
   }
})

export const endGame = createAsyncThunk('endGame', async (_, thunkAPI) => {
   try {
   } catch (e) {
      return thunkAPI.rejectWithValue(e)
   }
})

const stateSlice = createSlice({
   name: 'state',
   initialState,
   reducers: {
      moveLetterDown: (state, action: PayloadAction<string>) => {
         const idxTopCell = state.game.cells.top.findIndex((c) => c.currentLetter?.id === action.payload)

         if (idxTopCell === -1) return

         const currentLetterId = state.game.cells.top[idxTopCell].currentLetter?.id

         if (currentLetterId === undefined) return

         const idxBottomCell = state.game.cells.bottom.findIndex((c) => c.letter.id === currentLetterId)

         if (idxBottomCell === -1) return

         if (true) {
            const animatedLetter: IAnimatedLetter = {
               toCellId: state.game.cells.bottom[idxBottomCell].id,
               fromCellId: state.game.cells.top[idxTopCell].id,
            }

            state.animatedLettersData[currentLetterId] = animatedLetter
         }

         state.game.cells.top[idxTopCell].currentLetter = null
         state.game.cells.bottom[idxBottomCell].currentLetter = state.game.cells.bottom[idxBottomCell].letter
      },
      moveAllLettersDown: (state, action: PayloadAction) => {
         //key: letterId, value: cellId
         //по ключу буквы получить idx ячейки
         const mapLettersBottomCellsIndexes: { [letterId: string]: number } = {}

         const isAnimateEnabled = true
         const animatedLetters: AnimatedLettersData = {}

         state.game.cells.bottom.forEach((c, idx) => {
            mapLettersBottomCellsIndexes[c.letter.id] = idx
         })

         state.game.cells.top.forEach((c) => {
            if (c.currentLetter !== null) {
               const idxBottomCell = mapLettersBottomCellsIndexes[c.currentLetter.id]

               if (isAnimateEnabled) {
                  const fromCellId = c.id
                  const toCellId = state.game.cells.bottom[idxBottomCell].id

                  const animatedLetter: IAnimatedLetter = {
                     fromCellId,
                     toCellId,
                  }

                  animatedLetters[c.currentLetter.id] = animatedLetter
               }

               c.currentLetter = null
               state.game.cells.bottom[idxBottomCell].currentLetter = state.game.cells.bottom[idxBottomCell].letter
            }
         })

         state.animatedLettersData = animatedLetters
      },
      moveLetterTop: (state, action: PayloadAction<string>) => {
         const idxTopCell = state.game.cells.top.findIndex((c) => c.currentLetter === null)

         const idxBottomCell = state.game.cells.bottom.findIndex((c) => c.currentLetter?.id === action.payload)

         if (idxBottomCell === -1 || idxTopCell === -1) {
            return
         }

         if (true) {
            const currentLetterId = state.game.cells.bottom[idxBottomCell].letter.id

            const animatedLetter: IAnimatedLetter = {
               toCellId: state.game.cells.top[idxTopCell].id,
               fromCellId: state.game.cells.bottom[idxBottomCell].id,
            }

            state.animatedLettersData[currentLetterId] = animatedLetter
         }

         state.game.cells.bottom[idxBottomCell].currentLetter = null
         state.game.cells.top[idxTopCell].currentLetter = state.game.cells.bottom[idxBottomCell].letter

         state.game.numberOfLetterInserts++
      },

      removeAnimatedLetter: (state, action: PayloadAction<string>) => {
         if (typeof action.payload === 'string' && state.animatedLettersData.hasOwnProperty(action.payload)) {
            const { [action.payload]: letter, ...restLettersData } = state.animatedLettersData

            state.animatedLettersData = restLettersData
         }
      },

      setSolution: (state, action) => {},

      hintLetter: (state) => {
         // let hinted = false
         // for (const cell of state.game.answerCells) {
         //    if (cell.currentLetter && cell.currentLetter.value !== cell.letter.value) {
         //       const shuffledCell = state.game.shuffledCells.findIndex((c) => c.letter.id === cell.currentLetter?.id)
         //       if (shuffledCell !== -1) {
         //          state.game.shuffledCells[shuffledCell].currentLetter = state.game.shuffledCells[shuffledCell].letter
         //          cell.currentLetter = null
         //       }
         //    }
         //    if (!cell.hinted && !hinted) {
         //       cell.hinted = true
         //       hinted = true
         //    }
         // }
      },
      hintWord: (state) => {},

      setSounds: (state, action: PayloadAction<boolean>) => {
         state.settings.sounds = action.payload
      },
      setVibration: (state, action: PayloadAction<boolean>) => {
         state.settings.vibration = action.payload
      },
      setAutoLockScreen: (state, action: PayloadAction<boolean>) => {
         state.settings.autoLockScreen = action.payload
      },
      setAnimations: (state, action: PayloadAction<boolean>) => {
         state.settings.animations = action.payload
      },
      setComplexitySelection: (state, action: PayloadAction<boolean>) => {
         state.settings.complexitySelection = action.payload
      },
   },
   extraReducers: (builder) => {
      builder.addCase(startGame.fulfilled, (state, action) => {
         state.game = action.payload
      })

      builder.addCase(getPromoBannerData.fulfilled, (state, action) => {
         console.log('getPromoBannerData.fulfilled', action)

         if (action.payload) {
            state.promoBannerData = action.payload
            // state.promoBannerFetched = Date.now()
         }
      })
      builder.addCase(getPromoBannerData.rejected, (state, action) => {
         console.log('getPromoBannerData.fulfilled', action)
      })
   },
})

export const {
   hintLetter,
   moveLetterTop,
   moveLetterDown,
   moveAllLettersDown,
   setSolution,
   removeAnimatedLetter,
   setSounds,
   setVibration,
   setAutoLockScreen,
   setAnimations,
   setComplexitySelection,
} = stateSlice.actions

export default stateSlice.reducer
