import React from 'react'
import { ActionSheet, ActionSheetItem, Avatar, NavIdProps } from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { useAnchorElement } from '../AnchorElementProvider'
import { ActionSheetCloseItem } from '../ActionSheetCloseItem'
import { Icon16Crown, Icon24AddSquareOutline } from '@vkontakte/icons'

import { Icon24VideoAdvertisement } from '@vkontakte/icons'
import { Icon24UserAddOutline, Icon24UserAddedOutline } from '@vkontakte/icons'

const formatter = new Intl.NumberFormat(undefined, {})
export const ActionSheetCrownBalance: React.FC<NavIdProps> = () => {
   const router = useRouter()
   const anchorElement = useAnchorElement()

   const onClose = () => {
      router.popPageIfPopup()
   }

   const meta = (
      <div
         style={{
            display: 'flex',
            alignItems: 'center',
            color: 'var(--vkui--color_accent_blue)',
            borderRadius: 25,
            backgroundColor: 'var(--vkui--color_background_modal)',
            padding: '2px 8px',
         }}
      >
         <div>+{Math.floor(Math.random() * 500)}</div>
         <Icon16Crown style={{ marginLeft: 4 }} width={16} height={16} />
      </div>
   )

   return (
      <ActionSheet
         header={
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
               <div style={{ marginRight: 4 }}>{`Баланс: ${formatter.format(123214)}`}</div>
               <Icon16Crown width={16} height={16} />
            </div>
         }
         onClose={onClose}
         iosCloseItem={<ActionSheetCloseItem />}
         toggleRef={anchorElement}
      >
         <ActionSheetItem
            before={
               <Avatar mode='image' size={32} shadow={false}>
                  <Icon24VideoAdvertisement width={24} height={24} fill='white' />
               </Avatar>
            }
            meta={meta}
         >
            Посмотреть рекламу
         </ActionSheetItem>
         <ActionSheetItem
            before={
               <Avatar mode='image' size={32} shadow={false}>
                  <Icon24UserAddOutline width={24} height={24} fill='white' />
               </Avatar>
            }
            meta={meta}
         >
            Вступить в сообщество
         </ActionSheetItem>
         <ActionSheetItem
            before={
               <Avatar mode='image' size={32} shadow={false}>
                  <Icon24AddSquareOutline width={24} height={24} fill='white' />
               </Avatar>
            }
            meta={meta}
         >
            Добавить на главный экран
         </ActionSheetItem>
      </ActionSheet>
   )
}
