import React from 'react'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import {
   AnimatedLettersData,
   ITopCell,
   moveAllLettersDown,
   moveLetterDown,
   moveLetterTop,
   removeAnimatedLetter,
} from 'store/slices/state'

import { CellsRowBottom } from './CellsRowBottom'
import { CellsRowTop } from './CellsRowTop'
import { CellsWrapper } from './CellsWrapper'
import { RUN_SHAKING_EVENT } from '../CellGroup/ShakingCellGroup'

interface GameCellsProps {}

interface IMapCells {
   [cellId: string]: HTMLDivElement | null
}

export interface BaseCellsRowProps<T> extends LetterHandlers {
   cells: T[]
   cellsRef: { [key: string]: HTMLDivElement | null }

   animatedLettersData: AnimatedLettersData
}

export interface LetterHandlers {
   onClickLetter?: (letterId: string) => void
   onTransitionLetterRun?: (letterId: string) => void
   onTransitionLetterEnd?: (letterId: string) => void
}

export enum TransitionStatus {
   NONE = 0,
   RUNNING = 1,
   COMPLETED = 2,
}

const checkFullTopCells = (cells: ITopCell[]): boolean => {
   for (const cell of cells) {
      if (cell.currentLetter === null) return false
   }
   return true
}

const checkCorrectnessOfSolution = (cells: ITopCell[]): boolean => {
   if (!Array.isArray(cells) || !cells.length) return false

   for (const cell of cells) {
      if (cell.currentLetter?.value !== cell.letter.value) return false
   }
   return true
}

export const TIMEOUT_CHECK_SOLUTION_AFTER_UPDATE = 500

export const GameCells: React.FC<GameCellsProps> = () => {
   const dispatch = useAppDispatch()

   const cellsRef = React.useRef<IMapCells>({})
   const timeoutRef = React.useRef<NodeJS.Timeout | null>(null)

   const cells = useAppSelector((state) => state.game.cells)
   const animatedLettersData = useAppSelector((state) => state.animatedLettersData)

   const lettersTransitionStatus = React.useRef<{ [letterId: string]: TransitionStatus }>({})

   React.useEffect(() => {
      timeoutRef.current = setTimeout(checkSolution, TIMEOUT_CHECK_SOLUTION_AFTER_UPDATE)

      return () => {
         if (timeoutRef.current !== null) {
            clearTimeout(timeoutRef.current)
            timeoutRef.current = null
         }
      }
   }, [cells])

   const checkSolution = () => {
      if (timeoutRef.current !== null) {
         clearTimeout(timeoutRef.current)
         timeoutRef.current = null
      }

      const full = checkFullTopCells(cells.top)
      if (!full) return

      const correct = checkCorrectnessOfSolution(cells.top)

      console.log('solution', { full, correct })

      if (!correct) {
         window.dispatchEvent(new CustomEvent(RUN_SHAKING_EVENT))
         dispatch(moveAllLettersDown())
      }
   }

   const onClickTopLetter = (id: string) => {
      dispatch(moveLetterDown(id))
   }

   const onClickBottomLetter = (id: string) => {
      dispatch(moveLetterTop(id))
   }

   const onTransitionLetterRun = (id: string) => {
      dispatch(removeAnimatedLetter(id))
      lettersTransitionStatus.current[id] = TransitionStatus.RUNNING
   }

   const onTransitionLetterEnd = (id: string) => {
      lettersTransitionStatus.current[id] = TransitionStatus.COMPLETED
   }

   const onShakeEnd = () => {}
   const onShakeRun = () => {}
   return (
      <>
         <CellsWrapper stretched>
            <CellsRowTop
               onShakeRun={onShakeRun}
               onShakeEnd={onShakeEnd}
               cells={cells.top}
               cellsRef={cellsRef.current}
               animatedLettersData={animatedLettersData}
               onClickLetter={onClickTopLetter}
               onTransitionLetterRun={onTransitionLetterRun}
               onTransitionLetterEnd={onTransitionLetterEnd}
            />
         </CellsWrapper>
         <CellsWrapper>
            <CellsRowBottom
               cells={cells.bottom}
               cellsRef={cellsRef.current}
               animatedLettersData={animatedLettersData}
               onClickLetter={onClickBottomLetter}
               onTransitionLetterRun={onTransitionLetterRun}
               onTransitionLetterEnd={onTransitionLetterEnd}
            />
         </CellsWrapper>
      </>
   )
}
