import React from 'react'
import { Button, NavIdProps, Panel, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui'
import { Icon28SettingsOutline } from '@vkontakte/icons'
import { useRouter } from '@happysanta/router'
import { PAGE_GAME, PAGE_OPTIONS } from 'router/pages'
import { ButtonGroup } from 'components/ui/ButtonGroup/ButtonGroup'
import { CellsWrapper } from 'components/ui/Cells/CellsWrapper'
import { CellGroup } from 'components/ui/CellGroup/CellGroup'
import { guessTheWordTitle } from 'helpers/riddle'
import { SquareCell } from 'components/ui/SquareCell/SquareCell'
import { CellLetter } from 'components/ui/CellLetter/CellLetter'
import { Letter } from 'components/ui/Letter/Letter'
import { useSetAnchorElement } from '../popout/AnchorElementProvider'

export const PanelMain: React.FC<NavIdProps> = ({ nav }) => {
   const router = useRouter()
   const setAnchorElement = useSetAnchorElement()

   const onClickOptions = () => {
      router.pushPage(PAGE_OPTIONS)
   }

   const onClickLevel = () => {
      router.pushPage(PAGE_GAME)
   }
   return (
      <Panel nav={nav}>
         <PanelHeader
            separator={false}
            after={
               <PanelHeaderButton aria-label='options' onClick={onClickOptions}>
                  <Icon28SettingsOutline />
               </PanelHeaderButton>
            }
         ></PanelHeader>
         {/* <div style={{ flex: '1' }} /> */}
         <CellsWrapper stretched>
            <CellGroup mode='vertical' gap='m'>
               {guessTheWordTitle.map((row, idx) => (
                  <CellGroup key={idx} gap='m'>
                     {row.map((letter) => (
                        <SquareCell
                           // gap
                           key={letter.id}
                        >
                           <CellLetter mode='shadow'>
                              <Letter {...letter} />
                           </CellLetter>
                        </SquareCell>
                     ))}
                  </CellGroup>
               ))}
            </CellGroup>
         </CellsWrapper>
         <ButtonGroup>
            <Button
               className='Button--round Button--active-opacity'
               stretched
               size='l'
               mode='primary'
               appearance='accent'
               activeEffectDelay={300}
               onClick={onClickLevel}
            >
               Уровень 1
            </Button>
            <Button
               className='Button--round Button--active-opacity'
               stretched
               size='l'
               mode='primary'
               appearance='accent'
               activeEffectDelay={300}
               onClick={(e) => {
                  setAnchorElement(e.currentTarget)
                  router.pushPopup('c')
               }}
            >
               Новая игра
            </Button>
         </ButtonGroup>
      </Panel>
   )
}
