import React from 'react'
import { AdaptivityProvider, AppRoot, ConfigProvider, Platform, WebviewType } from '@vkontakte/vkui'
import bridge, { AppearanceType, VKBridgeSubscribeHandler } from '@vkontakte/vk-bridge'
import { deriveAppearance } from 'helpers/vkui'
import { AnchorElementProvider } from './popout/AnchorElementProvider'

export const VKUIProvider: React.FC = ({ children }) => {
   const [appearance, setAppearance] = React.useState<AppearanceType>('light')

   React.useEffect(() => {
      document.addEventListener('visibilitychange', (e) => {
         console.log('visibilitychange', document.visibilityState)
      })
      // setInterval(() => {
      //    setAppearance((prev) => (prev === 'dark' ? 'light' : 'dark'))
      // }, 2000)
   }, [])

   const bridgeHandler = React.useCallback<VKBridgeSubscribeHandler>((e) => {
      if (e.detail.type.startsWith('VK')) {
         console.log(e.detail.type, ' : ', e.detail.data)
      }

      switch (e.detail.type) {
         case 'VKWebAppUpdateConfig': {
            const appearance = deriveAppearance(e.detail.data.scheme)
            setAppearance(appearance)
            if ('appearance' in e.detail.data) {
            }
            console.log(e.detail.data)
         }
      }
   }, [])

   React.useEffect(() => {
      bridge.subscribe(bridgeHandler)

      bridge.send('VKWebAppInit')

      return () => {
         bridge.unsubscribe(bridgeHandler)
      }
   }, [bridgeHandler])

   return (
      <ConfigProvider platform={Platform.IOS} webviewType={WebviewType.INTERNAL} appearance={appearance}>
         <AdaptivityProvider>
            <AppRoot mode='full' noLegacyClasses scroll='global'>
               <AnchorElementProvider>{children}</AnchorElementProvider>
            </AppRoot>
         </AdaptivityProvider>
      </ConfigProvider>
   )
}
